<template>
  <div class="help">
    <div class="help_head relative">
      <img :src="helpBg">
      <div class="absolute all flex_center white" style="font-size:46px">帮助中心</div>
    </div>
    <div class="main_width padding_v_50">
      <div class="border circle_5 margin_v_15 lineheight_2 help_center">
        <div v-for="d in data">
          <div class="padding_15 border_b font_18 cursor flex help_title" @click="openAnswer">
            <div class="flex_1">{{ d.title }}</div>
            <div class="circle open_icon arrow_down"></div>
          </div>
          <div class="bg_light light padding_15 border_b hide content">{{ d.content }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'help',
  data () {
    return {
      helpBg: require('@/assets/images/help.png'),
      data: [{title: '什么是“区块链诚信认证“?', content: '区块链诚信认证，是将企业中代表“诚信”的元素，存储到区块链上。比如：荣誉证书，网站认证，系统安全认证等，这些元素将通过数字加密存在区块链上，确保其安全，无法篡改，将诚信存证，增加企业信用。'}, {title: '怎样使用“诚信网站”认证服务？', content: '“区块链诚信”认证的申请通过后，用户会收到系统自动发出的email和平台消息中心提示，用户将“区块链诚信”认证的图标和代码放到对应网站的首页上即可。'}, {title: '怎样申请“区块链诚信网站”认证？', content: '用户提交申请材料至保全网区块链诚信网站认证服务平台处进行申请。'}, {title: '申请“区块链诚信”认证需要有什么资质要求？', content: '明确的说，申请区块链诚信认证，只要是诚信企业均可申请，一般来说需要拥有工商营业执照的企业或拥有组织机构代码证的机关、事业单位、社团、组织等，“诚信元素”越多，诚信度越高'}, {title: '为什么需要做“区块链诚信”认证？', content: '现阶段社会中存在大量的企业信用信息，而这些信息相互孤立和分散，查阅和展示比较麻烦，对于企业来说，无法将这些信用信息整合形成一个可查，可信的统一数据源，导致企业信用无法聚合，相互之间形成了孤岛。'}, {title: '“区块链诚信”认证有效期', content: '区块链诚信认证有效期为1年，一年之后会再次进行提交资料，进行审核重新评分'}, {title: '“区块链诚信”认证认证页面的内容？', content: '基本信息包括：网站域名，网名称，诚信网站认证，申请单位工商信息;网站安全信息：第三方网络安全认证;资金安全信息：是否有资金存管，监管，托管等'}, {title: '“区块链诚信”认证的作用？', content: '区块链诚信认证，不同于现在市面上由工信部或者第三方认证平台发放的诚信网站认证，它是对企业“诚信”元素认证，企业的诚信程度由大众评判，并讲结果存储在区块链上，可以提升企业信用，永久不会改变。'}]
    }
  },
  methods: {
    openAnswer (e) {
      var eleCon = e.target.parentNode.parentNode.lastChild
      var eleOpen = e.target.parentNode.lastChild
      if (eleCon.classList.contains('hide')) {
        eleCon.classList.remove('hide')
        eleOpen.classList.add('arrow_up')
        eleOpen.classList.remove('arrow_down')
      } else {
        eleCon.classList.add('hide')
        eleOpen.classList.remove('arrow_up')
        eleOpen.classList.add('arrow_down')
      }
    }
  }
}
</script>

<style type="text/css">
  .help_center{
    width:1000px;
    margin: 0 auto
  }
  .help_title:hover .open_icon{
    background: #3c5dc9;
  }
  .open_icon{
    width: 30px;
    height: 30px;
    background: #e3e3e3;
  }
  .open_icon.arrow_up{
    background: #3c5dc9
  }
  .arrow_down:before,.arrow_up:before{
    right: 9px;
    width: 10px;
    height: 10px;
    border: 2px solid #fff;
    border-top: none;
    border-right: none;
  }
  .arrow_down:before{
    top: calc(50% - 8px);
  }
  .arrow_up:before{
    top: calc(50% - 4px);
  }
  .content.showshow{
    animation:show 1s ease;
  }
  @-webkit-keyframes show {
    from {
      display: none;
    }
    to {
      display: inline;
    }
  }
</style>



